/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.editor-styles-wrapper .wp-block-ridges-heading-with-button {
	background-color: var(--background-color);
}

.editor-styles-wrapper .heading-with-button__content .block-editor-inner-blocks {
	width: 100%;
}

.editor-styles-wrapper .heading-with-button__content .block-editor-block-list__layout {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

@media only screen and (min-width: 48rem) {

  .editor-styles-wrapper .wp-block-ridges-heading-with-button {
    padding: 3rem 0;
  }

}
